<template>
  <div class="shipping-carrier">
    <template v-for="(carrier, carrierIndex) of carriers" :key="carrierIndex">
      <div
        v-for="(carrierChild, carrierChildIndex) of carrier.carriers"
        :key="carrierChildIndex"
        :class="[
          'shipping-carrier__card',
          carrierChild.selected ? 'shipping-carrier__card--selected' : ''
        ]"
        @click="handleSelectCarrier(carrierChild)"
      >
        <div class="shipping-carrier__icon">
          <img
            :src="
              icon && carrierChild.hasOwnProperty(icon)
                ? carrierChild[icon]
                : ''
            "
            class="h-12 my-8"
            alt=""
          />
        </div>
        <div class="shipping-carrier__label">
          <i
            v-if="carrierChild.selected"
            class="material-icons text-teal-04 self-start mx-2"
            >check_circle</i
          >
          <div>
            <h2 class="text-lg font-normal">
              {{
                name && carrierChild.hasOwnProperty(name)
                  ? carrierChild[name]
                  : ''
              }}
            </h2>
            <div
              v-if="carrierChild.user_name || carrier.carrier_name"
              class="py-2 px-4 bg-grey-lighten text-grey-04 rounded-full my-2 text-center"
              style="word-wrap: break-word; word-break: normal;"
            >
              {{ carrierChild.user_name || carrier.carrier_name }}
            </div>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>
<script>
import { defineComponent, ref, computed, watch } from 'vue'

import Fieldset from 'bbUi/components/fieldset/fieldset.vue'

export default defineComponent({
  name: 'ShippingCarrier',
  components: {
    Fieldset
  },
  props: {
    carriers: {
      type: Array,
      default: () => []
    },
    by: {
      type: String,
      default: 'value'
    },
    name: {
      type: String,
      default: 'name'
    },
    icon: {
      type: String,
      default: 'icon'
    },
    carriersKey: {
      type: String,
      default: 'carriers'
    },
    selected: {
      type: String,
      default: ''
    }
  },
  emits: ['select'],
  setup(props, { emit }) {
    const selectedCarrier = ref(props.selected)
    const carriers = computed(() => {
      return props.carriers.map((shippingCarrier) => {
        const shippingCarrierDetail = { ...shippingCarrier }
        const isDefinedCarriersKey = !!props.carriersKey
        const isMatchedKey = shippingCarrierDetail.hasOwnProperty(
          props.carriersKey
        )
        if (!(isDefinedCarriersKey && isMatchedKey))
          return shippingCarrierDetail
        const shippingCarriers = shippingCarrierDetail[props.carriersKey]
        const formattedShippingCarriers = shippingCarriers.map((carrier) => ({
          ...carrier,
          selected:
            props.by &&
            carrier.hasOwnProperty(props.by) &&
            selectedCarrier.value
              ? carrier[props.by] === selectedCarrier.value
              : false
        }))
        shippingCarrierDetail[props.carriersKey] = formattedShippingCarriers
        return shippingCarrierDetail
      })
    })
    const handleSelectCarrier = (carrier = null) => {
      if (!carrier) return
      const isUnmatchedKey = !carrier.hasOwnProperty(props.by)
      if (isUnmatchedKey) return
      selectedCarrier.value = carrier[props.by]
      emit('select', selectedCarrier.value)
    }
    watch(
      () => props.selected,
      (selected) => {
        selectedCarrier.value = selected
      }
    )
    return {
      selectedCarrier,
      carriers,
      handleSelectCarrier
    }
  }
})
</script>
