.shipping-carrier {
  @apply grid md:grid-cols-4 lg:grid-cols-5 xl:grid-cols-6 grid-cols-1 gap-4 overflow-y-auto pr-2;
  max-height: 50vh;
  $self: &;
  &__card {
    @apply flex flex-col items-center transition duration-300 border border-grey-02 bg-white px-4 py-4 cursor-pointer;
    border-radius: 0.4em;
    min-height: 160px;
    &--selected,
    &:hover {
      @apply border-teal-04 text-teal-04;
    }
    &--selected {
      #{$self}__label {
        @apply md:text-left md:items-start;
        word-break: break-all;
      }
    }
  }
  &__icon,
  &__label {
    @apply flex-auto flex items-center text-center my-2;
  }
  &__icon {
    @apply my-0;
  }
}
</style>
